<template>
<div>


    <Header :FlagAdd=true ></Header>
    <section class="address-nr">
        <div>
            <div class="address-con">
                <input v-on:input="addtxt()" v-model="name" type='text' placeholder="请填写你的姓名" />
                <figcaption v-if="Show">请填写您的姓名</figcaption>

                <input @click="searchAddress()" v-model="tower" type='text' placeholder="小区/写字楼/学校等" />

                <input v-on:input="addretxt()" v-model="address"  type='text' placeholder="请填写详细送餐地址" />
                <figcaption v-if="bS">请填写详细送餐地址</figcaption>

                <input v-on:input="telephone()" v-model="phone" type='text' placeholder="请填写能够联系到您的手机号" />
                <figcaption v-if="phoneBsys">请填写能够联系到您的手机号</figcaption>

                <input v-model="phone_bk" type='text' placeholder="备用联系电话（选填）" />
            </div>
            <van-button @click="newAddr()" type="primary" class="New-address">新增地址</van-button>
        </div>
    </section>
    </div>
</div>
</template>
<script>
    import Header from './../../../Unify/Header'

    export default {
            components:{
            Header,
        },
        data() {
            return {
                name:'',
                tower:'',
                address:'',
                phone:'',
                phone_bk:'',
                geohash:'',

                Show:false,
                bS:false,
                phoneBsys:false,

                list:[],
            }
        },
    methods: {
    //姓名判断
        addtxt(){
        // console.log(this.name.length)
            if(this.name.length<=0){
                this.Show=true
            }else{
                this.Show=false
        }
    },
    //送餐地址判断
    addretxt(){
        if(this.address.length<=0){
            this.bS=true
        }else{
            this.bS=false
        }
    },
    //电话号
    telephone(){
        var phones=/^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/
        // console.log(this.phone)
        if(!phones.test(this.phone)){
            this.phoneBsys=true
        }else{
            this.phoneBsys=false
        }
    },
    //搜索地址
    searchAddress(){
        this.$router.push('/addDetail')

    },
    //新增地址
    newAddr(){
   
    var user_id=this.$store.state.ma.user_id;
    console.log(this.$store.state.ma.user_id);
    
    this.$http.post(`http://elm.cangdu.org/v1/users/${user_id}/addresses`,{
        address: this.address,
        address_detail: this.tower,
        geohash: this.geohash,
        name: this.name,
        phone: this.phone,
        phone_bk: this.phone_bk,
        poi_type: 0,
        sex: 1,
        tag: "公司",
        tag_type: 4

    }).then((res)=>{
        console.log(res)
    })
    

    //vuex本地存储
    this.$router.push({path: "/address"})
    }
    },

    mounted() {
    this.tower=this.$route.query.addname;
    // console.log(this.$route.query.addname)
    this.geohash=this.$route.query.geohash;

    },
    }
</script>

<style lang="scss">
.address-nr{
    margin-top: 0.7rem;
overflow: auto;
float: 1;
}
.address-con{
height: auto;width:100%;
background: #fff;
padding-bottom:0.15rem; 
>input{
width:94%;
margin-left: 3%;
margin-top:0.188rem; 
background: #f2f2f2;
height: 0.5rem;
border: red solid 0.008rem;
border: none;
text-indent: 0.2rem;
border-radius: 0.04rem;
font-size: 0.2rem;
}
figcaption{
box-sizing: border-box;
padding-top:0.05rem;
margin-left: 3.4%;
color: red; 
}
}
.New-address{
width: 94%;
height: 0.6rem;width:94%;
margin-left: 3%;
margin-top:0.188rem; 
border-radius: 0.04rem;
font-size: 0.24rem;
font-weight: bold;
}

</style>